body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.piano{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}




.pianokey{
    border: 1px solid black;
    height: 10rem;
    display: inline-block;
    width: 3%;
    border-radius: 0 0 5px 5px;
}

.pianokey:not(:last-child){
    border-right: 0px;
}

.pianokey>.pianokey__content{
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    border-radius: 0 0 5px 5px;
}
.pianokey_whitekey{
    background-color: white;
    position: relative;
}

.pianokey_whitekey>.pianokey__content>.pianokeybind{
    color:black;
}
.pianokey_blackkey{
    border-right: none;
    border-left:none;
    width: 0;
    background-color: black;
}

.pianokey>.pianokey__content>.pianokeybind{
    font-size: .5rem;
    line-height: 1.5rem;
}
.pianokey_blackkey>.pianokey__content{
    position: absolute;
    height: 5rem;
    width: 2%;
    background-color: black;
    margin-left: -1%;
    z-index: 2;
}

/* 编辑时 白色按钮样式 */
.pianokey_whitekey>.pianokey__content_editing:after{
    
}

/* 编辑时 黑色按钮样式 */
.pianokey_blackkey>.pianokey__content_editing{
    border:red;
}


/* 按下时样式 */
.pianokey>.pianokey__content_press{
    background-color: #ff6666;
}

.pianokey_blackkey>.pianokey__content>.pianokeybind{
    color:white;
    /* display: inline; */
}



.pianokey>.pianokey__content>.pianokey__mask{
   display: none;
}
.pianokey_whitekey>.pianokey__content_editing>.pianokey__mask{
   
    width: 100%;
    height: 100%;
    position: absolute;
    font-size: .5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    background-color: #eeeeee80;
}

.pianokey_blackkey>.pianokey__content_editing>.pianokey__mask{
   
    width: 100%;
    height: 100%;
    position: absolute;
    font-size: .5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    background-color: #eeeeee80;
}

.pianokey_whitekey>.pianokey__content_editing>.pianokey__mask>.pianokey__mask__content{
    font-size: .5rem;
    padding-bottom: 1.5rem;
}

.pianokey_blackkey>.pianokey__content_editing>.pianokey__mask>.pianokey__mask__content{
    color:#ffffff;
    font-size: .5rem;
    padding-bottom: 1.5rem;
}
.ctl{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.ctl_btn{
    margin:.5rem;
}
